import {
	mapMutations
} from 'vuex'
export default {
	props: {
		title: {
			required: true,
			type: String
		},
		rankingRising: {
			required: true,
			type: Object
		},
		rankingHot: {
			required: true,
			type: Object
		},
		rankingNewSong: {
			required: true,
			type: Object
		},
		rankingRisingTracks: {
			required: true,
			type: Object
		},
		rankingHotTracks: {
			required: true,
			type: Object
		},
		rankingNewSongTracks: {
			required: true,
			type: Object
		}
	},
	data() {
		return {

		};
	},
	methods: {
		...mapMutations(['setRanking']),
		goRankingDetail(idx) {
			uni.navigateTo({
				url: '/pages/ranking/ranking',
				success: res => {
					if (idx === 0) {
						this.setRanking(this.rankingRising);
					} else if (idx === 1) {
						this.setRanking(this.rankingHot);
					} else if (idx === 2) {
						this.setRanking(this.rankingNewSong);
					}
				},
				fail: (e) => {
					console.log(e);
				},
				complete: () => {}
			});
		}
	}
}
